<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/notice' }">通知管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改通知' : '新增通知' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="通知标题" prop="title">
          <el-input size="small" style="width: 300px" v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="开始时间">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'startDate')
              }
            "
            v-model="ruleForm.startDate"
            type="date"
            placeholder="选择开始有效时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间" prop="endDate">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'endDate')
              }
            "
            v-model="ruleForm.endDate"
            type="date"
            placeholder="选择结束有效时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="选择机器">
          <div class="btn_cont">
            <el-link type="primary" :underline="false" @click.stop="chooseMachine">选机器</el-link>
            <span v-if="machineStr">
              <i class="machine">{{ machineStr }}</i>
            </span>
            <span v-if="machineStr" class="checkAllStore" @click.stop="checkAllStore">查看已选机器</span>
          </div>
        </el-form-item>
        <el-form-item label="通知内容" prop="content">
          <el-input type="textarea" size="small" v-model="ruleForm.content" :autosize="{ minRows: 4, maxRows: 8 }"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
      <ChooseMachine ref="ChooseMachine" @getName="getName" />

      <el-dialog title="已选机器" :visible.sync="showDialog" width="50%">
        <el-divider></el-divider>
        <div class="machine_cont">
          <div class="machine_item" v-for="(item, index) in deviceList" :key="index">
            {{ item }}
          </div>
        </div>
        <span slot="footer" class="dialog-footer"> </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import ChooseMachine from '../../components/choose-machine/ChooseMachine'
export default {
  components: { ChooseMachine },
  data() {
    return {
      ruleForm: {
        title: '',
        content: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        infoStatus: 0,
        type: 1,
        ifAllDevice: 0,
        deviceId: ''
      },
      rules: {
        title: [{ required: true, message: '请输入标题', trigger: 'blur' }],
        content: [{ required: true, message: '请输入通知内容', trigger: 'blur' }],
        chooseDate: [{ required: true, message: '请选择有效日期', trigger: 'blur' }]
        // detialPic: [{ required: true, message: '请上传商品详情图', trigger: 'blur' }],
        // Cooler: [{ required: true, message: '请选择饮品冷热', trigger: 'blur' }]
      },
      machineStr: '',
      deviceList: [],
      showDialog: false
    }
  },

  methods: {
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          params.startDate = params.startDate ? this.$moment(params.startDate).format('YYYY-MM-DD') : ''
          params.endDate = params.endDate ? this.$moment(params.endDate).format('YYYY-MM-DD') : ''
          params['deviceList'] = this.ruleForm.deviceId.split(',')
          if (this.ruleForm.deviceId.length == 0) {
            this.$message.error('请选择要发布的机器')
            return false
          }
          if (this.type == 'add') {
            this.httpPost('/info/add', params)
              .then(res => {
                this.$message.success('设置成功')
                this.$router.push({
                  path: '/notice',
                  query: {}
                })
              })
              .catch(() => {
                console.log('cancel')
                return this.$errorTip(), false
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    getName(e, f) {
      this.machineStr = f
      this.ruleForm.deviceId = e.toString()
    },

    // 选择机器
    chooseMachine() {
      this.$refs.ChooseMachine.selected(this.ruleForm.deviceId)
    },
    changeDate(e, type) {
      // debugger
      console.log(this.ruleForm.startDate, this.ruleForm.endDate)
      if (this.ruleForm.endDate && type === 'startDate' && e && e.getTime() > new Date(this.ruleForm.endDate).getTime()) {
        this.$message.warning('开始时间不得大于结束时间')
        this.ruleForm.startDate = ''
      } else if (this.ruleForm.startDate && e && e.getTime() < new Date(this.ruleForm.startDate).getTime()) {
        this.$message.warning('结束时间不得小于开始时间')
        this.ruleForm.endDate = ''
      }
    },

    // 查看已选机器
    checkAllStore() {
      this.deviceList = this.machineStr.split(',')
      this.showDialog = true
    },

    // 返回
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    const { params } = this.$route
    if (!params.type) this.$router.back()
    if (params.type === 'edit') {
      this.type = 'edit'
      this.ruleForm = params.info
      this.machineStr = params.info.deviceId
    } else {
      this.type = 'add'
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item__content {
  .el-textarea {
    width: 350px;
  }
  .el-select {
    width: 350px;
  }
}
.hidden {
  visibility: hidden;
}
.row_item {
  display: flex;
  flex-wrap: wrap;
  /deep/.el-form-item__content {
    width: 100px;
  }
}
.move_btn {
  margin-left: 50px;
  img {
    width: 20px;
    height: 20px;
    padding: 4px;
    border: 1px dashed #333;
    border-radius: 5px;
    margin: 0 5px;
    cursor: pointer;
  }
}
.stock_cont {
  /deep/.el-form-item__content {
    padding-top: 2px;
  }
}
.btn_cont {
  display: flex;
  align-items: center;
  span {
    height: 40px;
    line-height: 40px;
    .machine {
      margin: 0 10px;
      width: 200px;
      display: inline-block;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
  .checkAllStore {
    color: $color;
    cursor: pointer;
  }
}
.machine_cont {
  display: flex;
  flex-wrap: wrap;
  .machine_item {
    padding: 10px 20px;
  }
}
</style>
