<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/infopublish' }">信息发布</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改信息' : '发布信息' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="信息标题" prop="title">
          <el-input size="small" style="width: 300px" v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="开始时间">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'startDate')
              }
            "
            v-model="ruleForm.startDate"
            type="date"
            placeholder="选择开始有效时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间" prop="endDate">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'endDate')
              }
            "
            v-model="ruleForm.endDate"
            type="date"
            placeholder="选择结束有效时间"
          >
          </el-date-picker>
        </el-form-item>

        <!-- 选择机器 -->

        <el-form-item label="选择机器">
          <div class="btn_cont">
            <el-link type="primary" :underline="false" @click.stop="chooseMachine">选机器</el-link>
            <span v-if="machineStr">
              <i class="machine">{{ machineStr }}</i>
            </span>
            <span v-if="machineStr" class="checkAllStore" @click.stop="checkAllStore">查看已选机器</span>
          </div>
        </el-form-item>

        <!-- 图片 -->

        <el-form-item label="图片" prop="picList">
          <el-upload class="pics" ref="uploadPictures" :file-list="ruleForm.picList" multiple accept=".png,.jpg,.jpeg" list-type="picture-card" :action="uploadAction()" :on-remove="handleRemove" :on-success="uploadSuccess" :before-upload="beforeUpload">
            <i class="el-icon-plus"></i>
          </el-upload>
          <div>支持 png/jpg 格式，<el-link type="primary" :underline="false" @click="queryMaterial(1, '')">从素材库选择</el-link></div>
        </el-form-item>

        <!-- 视频 -->

        <el-form-item label="视频上传" prop="Video">
          <!-- action必选参数, 上传的地址 -->
          <el-upload class="upload-demo" :action="uploadAction()" :show-file-list="true" :on-success="handleVideoSuccess" :before-upload="beforeUploadVideo" :on-progress="uploadVideoProcess" :on-remove="handleVideoRemove">
            <div class="el-upload__text"><i class="el-icon-upload2"></i> 上传</div>
          </el-upload>
          <div class="text">请保证视频格式正确，且不超过100M<el-link type="primary" :underline="false" @click="queryMaterial(2, '')">从素材库选择</el-link></div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>

      <ChooseMachine ref="ChooseMachine" @getName="getName" />

      <el-dialog title="已选机器" :visible.sync="showDialog" width="50%">
        <el-divider></el-divider>
        <div class="machine_cont">
          <div class="machine_item" v-for="(item, index) in machineList" :key="index">
            {{ item }}
          </div>
        </div>
        <span slot="footer" class="dialog-footer"> </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce/Tinymce'
import ChooseMachine from '@/components/choose-machine/ChooseMachine'
export default {
  components: { Tinymce, ChooseMachine },
  data() {
    return {
      ruleForm: {
        title: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        picList: [],
        videoList: [],
        infoStatus: 0,
        type: 2,
        ifAllDevice: 0,
        deviceIds: '',
        tFiles:[]
      },
      rules: {
        title: [{ required: true, message: '请输入标题', trigger: 'blur' }],
        info: [{ required: true, message: '请输入内容', trigger: 'blur' }],
        chooseDate: [{ required: true, message: '请选择有效时间', trigger: 'blur' }]
        // Cooler: [{ required: true, message: '请选择饮品冷热', trigger: 'blur' }]
      },
      options: [],

      videoCount: 1,
      videoUploadPercent: 0,
      machineStr: '',
      machineList: [],
      showDialog: false
    }
  },

  methods: {
    //根据format查出素材库的图片或者视频
    queryMaterial(format, fileName) {
      let params = {
        format: format,
        originalFileName: fileName,
        page: 1,
        size: 20
      }
      this.httpPost('/material/query', params).then(ret => {})
    },
    // 视频------------------------------------------------------------------------------------------

    // 验证方法：验证视频格式和视频大小
    beforeUploadVideo(file) {
      const isLt100M = file.size / 1024 / 1024 < 100
      if (['video/mp4', 'video/ogg', 'video/flv', 'video/avi', 'video/wmv', 'video/rmvb'].indexOf(file.type) == -1) {
        this.$message.warning('请上传正确的视频格式')
        return false
      }
      if (!isLt100M) {
        this.$message.warning('上传视频大小不能超过100MB哦!')
        return false
      }
    },

    // 上传进度显示
    uploadVideoProcess(event, file, fileList) {
      this.videoCount !== this.videoCount++
    },

    handleVideoSuccess(res, file) {
      console.log(file.response.data)
      this.ruleForm.tFiles.push(file.response.data)
      this.ruleForm.videoList.push(res)
      console.log(this.ruleForm.videoList)
    },
    // 删除数组中视频
    handleVideoRemove(file, fileList) {
      console.log(file)
      if (fileList.length != this.ruleForm.videoList.length) {
        let material = file.response.data
        // alert(1)
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.videoList.length; i < len; i++) {
          if (this.ruleForm.videoList[i].data === material) {
            index = i
            break
          }
        }
        this.ruleForm.videoList.splice(index, 1)
        console.log(this.ruleForm.videoList)
      }
    },
    // 图片------------------------------------------------------------------------------------------

    uploadAction() {
      return this.$picUpload
      // return 'http://localhost:8888/api/img/'
    },

    // 删除数组中图片
    handleRemove(file, fileList) {
      if (fileList.length != this.ruleForm.picList.length) {
        let material = file.response.data
        // alert(1)
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.picList.length; i < len; i++) {
          if (this.ruleForm.picList[i].response.data === material) {
            index = i
            break
          }
        }
        this.ruleForm.picList.splice(index, 1)
        console.log(this.ruleForm.picList)
      }
      if (fileList.length < 5) {
        document.querySelector('.pics .el-upload--picture-card').style.display = 'block'
      }
    },

    // 上传图片成功
    uploadSuccess(res, file, fileList) {
      console.log(file.response.data)
      this.ruleForm.tFiles.push(file.response.data)
      this.ruleForm.picList = fileList
      if (this.ruleForm.picList.length >= 5) document.querySelector('.pics .el-upload--picture-card').style.display = 'none'
    },

    // 上传图片之前
    beforeUpload(file) {
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG / png 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
      if (this.ruleForm.picList.length >= 5) document.querySelector('.pics .el-upload--picture-card').style.display = 'none'
    },
    changeDate(e, type) {
      // debugger
      console.log(this.ruleForm.startDate, this.ruleForm.endDate)
      if (this.ruleForm.endDate && type === 'startDate' && e && e.getTime() > new Date(this.ruleForm.endDate).getTime()) {
        this.$message.warning('开始时间不得大于结束时间')
        this.ruleForm.startDate = ''
      } else if (this.ruleForm.startDate && e && e.getTime() < new Date(this.ruleForm.startDate).getTime()) {
        this.$message.warning('结束时间不得小于开始时间')
        this.ruleForm.endDate = ''
      }
    },

    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.ruleForm.startDate||!this.ruleForm.endDate) {
           return this.$message.error('请选择开始结束时间！')
          }
          let params = Object.assign({}, this.ruleForm)
          params.startDate = params.startDate ? this.$moment(params.startDate).format('YYYY-MM-DD') : ''
          params.endDate = params.endDate ? this.$moment(params.endDate).format('YYYY-MM-DD') : ''
          // if (this.ruleForm.picList.length > 0) params['materialIds'] = params['picList'].map(o => o.response.data).join(',')
          // if (this.ruleForm.videoList.length > 0) {
          //   params['videoIds'] = params['videoList'].map(o => o.data).join(',')
          //   if (this.ruleForm.picList.length > 0) params['materialIds'] = params['materialIds'] + ',' + params['videoIds']
          //   else params['materialIds'] = params['videoIds']
          // }
          params['deviceList'] = this.ruleForm.deviceIds.split(',')
          // params.startDate = this.dateFormat(params.startDate)
          // params.endDate = this.dateFormat(params.endDate)
          console.log(params)
          if (this.ruleForm.deviceIds.length == 0) {
            this.$message.error('请选择要发布的机器')
            return false
          }
          if (this.ruleForm.picList.length == 0 && this.ruleForm.videoList.length == 0) {
            this.$message.error('请选择图片或视频')
            return false
          }
          if (this.type == 'add') {
            this.httpPost('/info/add', params)
              .then(res => {
                this.$message.success('设置成功')
                this.$router.push({
                  path: '/infopublish',
                  query: {}
                })
              })
              .catch(() => {
                console.log('cancel')
                return this.$errorTip(), false
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    dateFormat(date) {
      console.log(date)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? '0' + d : d
      return y + '-' + m + '-' + d
    },
    getName(e, f) {
      this.machineStr = f
      this.ruleForm.deviceIds = e.toString()
    },

    // 选择机器
    chooseMachine() {
      this.$refs.ChooseMachine.selected(this.ruleForm.deviceIds)
    },

    // 查看已选机器
    checkAllStore() {
      this.machineList = this.machineStr.split(',')
      this.showDialog = true
    },

    // 返回
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    const { params } = this.$route
    if (!params.type) this.$router.back()
    if (params.type === 'edit') {
      this.type = 'edit'
      this.ruleForm = params.info
    } else {
      this.type = 'add'
    }
  }
}
</script>

<style lang="scss" scoped>
.hidden {
  visibility: hidden;
}
.row_item {
  display: flex;
  flex-wrap: wrap;
  /deep/.el-form-item__content {
    width: 100px;
  }
}
.move_btn {
  margin-left: 50px;
  img {
    width: 20px;
    height: 20px;
    padding: 4px;
    border: 1px dashed #333;
    border-radius: 5px;
    margin: 0 5px;
    cursor: pointer;
  }
}
.stock_cont {
  /deep/.el-form-item__content {
    padding-top: 2px;
  }
}
.btn_cont {
  display: flex;
  align-items: center;
  span {
    height: 40px;
    line-height: 40px;
    .machine {
      margin: 0 10px;
      width: 200px;
      display: inline-block;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
  .checkAllStore {
    color: $color;
    cursor: pointer;
  }
}
.machine_cont {
  display: flex;
  flex-wrap: wrap;
  .machine_item {
    padding: 10px 20px;
  }
}
</style>
