<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>通知管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="80px" class="form_border">
        <el-form-item label="标题">
          <el-input v-model="queryForm.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="开始时间">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'startDate')
              }
            "
            v-model="queryForm.startDate"
            type="date"
            placeholder="选择开始有效时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间" prop="endDate">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'endDate')
              }
            "
            v-model="queryForm.endDate"
            type="date"
            placeholder="选择结束有效时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
      </el-form>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增通知</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="title" label="标题"> </el-table-column>
          <el-table-column prop="content" label="内容"> </el-table-column>
          <el-table-column prop="createDatetime" label="创建时间" width="250"> </el-table-column>
          <el-table-column label="有效时间" width="250">
            <template slot-scope="scope">
              <span>{{ scope.row.startDate }}</span>
              <span>{{ ' - ' }}</span>
              <span>{{ scope.row.endDate }}</span>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <!--<el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>-->
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        title: '',
        StartDate: '',
        EndDate: '',
        size: 10,
        page: 1,
        total: '',
        type: 1
      },
      tableData: []
    }
  },
  methods: {
    //删除
    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let informationIdList = []
          informationIdList.push(id)
          this.httpPost('/info/stop', informationIdList).then(res => {
            this.$message.success('删除成功')
            this.loadData()
          })
        })
        .catch(() => {})
    },
    // 搜索
    search() {
      // console.log(this.queryForm)
      if (!this.queryForm.startDate && this.queryForm.endDate) return this.$message({ type: 'warning', message: '请选择开始日期!' }), false
      if (this.queryForm.startDate && !this.queryForm.endDate) return this.$message({ type: 'warning', message: '请选择结束日期!' }), false
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.count = ''
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editNotice', name: 'EditNotice', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editNotice', name: 'EditNotice', params: { type: 'edit', info } })
    },
    changeDate(e, type) {
      // debugger
      console.log(this.queryForm.startDate, this.queryForm.endDate)
      if (this.queryForm.endDate && type === 'startDate' && e && e.getTime() > new Date(this.queryForm.endDate).getTime()) {
        this.$message.warning('开始时间不得大于结束时间')
        this.queryForm.startDate = ''
      } else if (this.queryForm.startDate && e && e.getTime() < new Date(this.queryForm.startDate).getTime()) {
        this.$message.warning('结束时间不得小于开始时间')
        this.queryForm.endDate = ''
      }
    },
    loadData() {
      let params = Object.assign({}, this.queryForm)
      params.startDate = params.startDate ? this.$moment(params.startDate).format('YYYY-MM-DD') : ''
      params.endDate = params.endDate ? this.$moment(params.endDate).format('YYYY-MM-DD') : ''
      this.httpGet('/info/query/', params).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 240px !important;
  }
}
</style>
